<template>
    <div class="container-fluid text-center loginForm">
        <form class="col-4 p-5 rounded-3 animate__animated animate__fadeInRight" onsubmit="return false">
            <h1>Log in</h1>
            <div class="mb-3">
                <label for="accountName" class="form-label">Account Name:</label>
                <input v-model="userName" type="text" class="form-control" id="accountName" aria-describedby="emailHelp">
            </div>
            <div class="mb-3">
                <label for="password" class="form-label">Password:</label>
                <input v-model="password" type="password" class="form-control" id="password">
            </div>
            <div class="alert alert-danger  tipBox" role="alert" v-if="error !== null">
                {{ error }}
            </div>
            <div class="mb-3" id="button">
                <button class="btn btn-primary" @click="toLogin">Log in</button>
                <button class="btn btn-secondary" @click="toSignup">Sign up</button>
            </div>
        </form>
    </div>
</template>
<script setup>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
const router = useRouter()
const store = useStore()

let userName = ref()
let password = ref()

let error = computed(() => {
    return store.state.errorMessage
})

let toSignup = () => {
    router.push('/signup')
}

let toLogin = () => {
    let user = {
        username: userName.value,
        password: password.value
    }
    store.dispatch('login', { user, router })
}

onMounted(() => {
    store.commit('error', null)
})

</script>
